.owl-list{display:flex;flex-wrap:wrap}.owl-list li{position:relative;flex:0 1 20%;text-align:center}.owl-list li label{display:block}.owl-list li label.pickfile{display:block;height:150px;padding:10px;border:3px dashed #ccc;margin-bottom:15px;background-size:contain;background-repeat:no-repeat;background-position:center;margin-right:10px}.owl-list li label.pickfile input{display:none}.owl-list li span.delete{display:block;position:absolute;bottom:0;right:0;width:10px;height:10px;line-height:10px;border-radius:100%;border:1px solid #ccc;cursor:pointer}
